import Home from '../views/Home.vue'

import Loves from '../views/pages/loves.vue'
import Comments from '../views/pages/comments.vue'
import Replies from '../views/pages/replies.vue'
import AboutLoveWall from '../views/pages/comments.vue'

let siderBar = [
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-heart',
        leaf: true,//
        children: [
            { path: '/loves', component: Loves, name: '' }
        ]
    },

    // {
    //     path: '/',
    //     component: Home,
    //     name: '',
    //     iconCls: 'fa fa-exclamation-circle',
    //     leaf: true,//
    //     children: [
    //         { path: '/badReports', component: Loves, name: '' }
    //     ]
    // },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-user',
        leaf: true,//
        children: [
            { path: '/users', component: Loves, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-user',
        leaf: true,//
        children: [
            { path: '/virtualUsers', component: Loves, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-male',
        leaf: true,//
        children: [
            { path: '/collegeUsers', component: Loves, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-bell',
        leaf: true,//
        children: [
            { path: '/systemNotices', component: Loves, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-rocket',
        leaf: true,//
        children: [
            { path: '/relatedApplets', component: Loves, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-home',
        leaf: true,//
        children: [
            { path: '/aboutLoveWalls', component: AboutLoveWall, name: '' }
        ]
    },
];

export default siderBar;


