<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.search" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getUsers"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
    
        <!---->
        <el-table :data="users" border highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
            <!--<el-table-column type="selection" width="55">
            </el-table-column>-->
            <el-table-column type="index" width="60">
            </el-table-column>
            
            <el-table-column prop="id" label="ID" width="100" sortable>
            </el-table-column>

            <el-table-column prop="nickname" label="" width="100" sortable>
            </el-table-column>

            <!--<el-table-column prop="role" label="" width="100" :formatter="formatRole" sortable>
            </el-table-column>-->
            <el-table-column prop="trust" label="" width="100" :formatter="formatTrust" sortable>
            </el-table-column>
            <el-table-column prop="available" label="" width="100" :formatter="formatAvailable" sortable>
            </el-table-column>
            <el-table-column prop="disabled_reason" label="" min-width="100" sortable>
            </el-table-column>
            <el-table-column prop="praise_nums" label="" width="100" sortable>
            </el-table-column>

            <el-table-column prop="realname" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="gender_name" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="college_name" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="major" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="grade_name" label="" width="100" sortable>
            </el-table-column>

            <el-table-column prop="avatarUrl" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="pictures" label="" min-width="120" sortable>
            </el-table-column>

            <el-table-column prop="wechat" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="qq" label="QQ" width="100" sortable>
            </el-table-column>
            <el-table-column prop="weibo" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="mobilePhone" label="" width="100" sortable>
            </el-table-column>

            
            

            <el-table-column prop="birthday" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="age" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="constellation" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="height" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="weight" label="" width="100" sortable>
            </el-table-column>
            <el-table-column prop="hometown" label="" width="100" sortable>
            </el-table-column>

            <el-table-column prop="signature" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="character" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="hobby" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="love_history" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="love_selecting" label="" min-width="120" sortable>
            </el-table-column>

            <el-table-column fixed="right" label="" width="100">
                <template scope="scope">
                    <el-button size="small" @click="handleEdit(scope.row)"></el-button>
                    <!--<el-button type="danger" size="small" @click="handleDel(scope.row)"></el-button>-->
                </template>
            </el-table-column>
        </el-table>
    
        <!---->
        <el-col :span="24" class="toolbar">
            <!--<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>-->
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
    
        <!---->
        <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
            <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">

                <el-form-item label="">
                    <el-input v-model="editForm.nickname" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="editForm.trust">
                        <el-radio class="radio" :label="0"></el-radio>
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="2"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="editForm.available">
                        <el-radio class="radio" :label="1"></el-radio>
                        <el-radio class="radio" :label="0"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="" v-if="editForm.available == 0" >
                    <el-input v-model="editForm.disabled_reason"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
        </el-dialog>
    
    </section>
</template>

<script>
import util from '../../common/js/util'
//import NProgress from 'nprogress'
// import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';
import { getUsersList, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';

export default {
    data() {
        return {
            filters: {
                search: ''
            },
            users: [],
            total: 0,
            page: 1,
            listLoading: false,
            sels: [],//

            editFormVisible: false,//
            editLoading: false,
            editFormRules: {
                // content: [
                //     { required: true, message: '', trigger: 'blur' }
                // ]
            },
            //
            editForm: {},

            addFormVisible: false,//
            addLoading: false,
            addFormRules: {
                // content: [
                //     { required: true, message: '', trigger: 'blur' }
                // ]
            },
            //
            addForm: {}
        }
    },
    methods: {
        //
        // formatRole: function (row, column) {
        //     return row.role == 1 ? '' : row.role == 0 ? '' : '';
        // },
        formatTrust: function (row, column) {
            if (row.trust == 0) {
                return ''
            } else if (row.trust == 1) {
                return ''
            } else {
                return ''
            }
            // return row.role == 1 ? '' : row.role == 0 ? '' : '';
        },
        formatAvailable: function (row, column) {
            return row.available == 1 ? '' : row.available == 0 ? '' : '';
        },
        handleCurrentChange(val) {
            this.page = val;
            this.getUsers();
        },
        //
        getUsers() {
            let para = {
                page: this.page,
                search: this.filters.search
            };
            this.listLoading = true;
            getUsersList(para).then((res) => {
                console.log('getUsersList res', res);
                this.total = res.data.dataLength;
                this.users = res.data.data;
                this.listLoading = false;
            });

        },
        //
        // handleDel: function (row) {
        //     this.$confirm('?', '', {
        //         type: 'warning'
        //     }).then(() => {
        //         this.listLoading = true;
        //         //NProgress.start();
        //         let para = { id: row.id };
        //         removeUser(para).then((res) => {
        //             this.listLoading = false;
        //             //NProgress.done();
        //             this.$message({
        //                 message: '',
        //                 type: 'success'
        //             });
        //             this.getUsers();
        //         });
        //     }).catch(() => {

        //     });
        // },
        //
        handleEdit: function (row) {
            this.editFormVisible = true;
            console.log('row', row);
            this.editForm = Object.assign({}, row);
        },
        //
        // handleAdd: function () {
        //     this.addFormVisible = true;
        //     this.addForm = {
        //         content: '',
        //         video_url: '',
        //         anonymous: 0
        //     };
        // },
        //
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    this.$confirm('', '', {}).then(() => {
                        this.editLoading = true;
                        //NProgress.start();
                        let para = Object.assign({}, this.editForm);
                        editUser(para).then((res) => {
                            this.editLoading = false;
                            //NProgress.done();
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.$refs['editForm'].resetFields();
                            this.editFormVisible = false;
                            this.getUsers();
                        });
                    });
                }
            });
        },
        //
        // addSubmit: function () {
        //     this.$refs.addForm.validate((valid) => {
        //         if (valid) {
        //             this.$confirm('', '', {}).then(() => {
        //                 this.addLoading = true;
        //                 //NProgress.start();
        //                 let para = Object.assign({}, this.addForm);
        //                 addUser(para).then((res) => {
        //                     this.addLoading = false;
        //                     //NProgress.done();
        //                     this.$message({
        //                         message: '',
        //                         type: 'success'
        //                     });
        //                     this.$refs['addForm'].resetFields();
        //                     this.addFormVisible = false;
        //                     this.getUsers();
        //                 });
        //             });
        //         }
        //     });
        // },
        // selsChange: function (sels) {
        //     this.sels = sels;
        // },
        //
        // batchRemove: function () {
        //     // var ids = this.sels.map(item => item.id).toString();
        //     var ids = this.sels.map(item => item.id);
        //     this.$confirm('', '', {
        //         type: 'warning'
        //     }).then(() => {
        //         this.listLoading = true;
        //         //NProgress.start();
        //         let para = { ids: ids };
        //         batchRemoveUser(para).then((res) => {
        //             this.listLoading = false;
        //             //NProgress.done();
        //             this.$message({
        //                 message: '',
        //                 type: 'success'
        //             });
        //             this.getUsers();
        //         });
        //     }).catch(() => {

        //     });
        // }
    },
    mounted() {
        this.getUsers();
    }
}

</script>

<style scoped>

</style>


